.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.app-container {
    background-color: black;
    height: 100vh;
    width: 100vw;
    font-family: monospace;
}

.message-input {
    border: 0;
    outline-width: 0;
    background-color: black;
    color: white;
    float: left;
    caret-shape: block;
    width: 100vw;
}

.incoming-message {
    color: white;
    text-align: left;
    margin-bottom: 0;
}

.chatbox-container {
    padding-left: 10px;
}

.input-container {
    padding-left: 10px;
}
